import React from "react";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";

const RightNav = ({ productsQuantity, toggleCart }) => {
  return (
    <div className='tm-store__right-nav'>
      <nav role='navigation'>
        <ul className='tm-store__right-nav--list'>
          <li
            id='menu-item-3241'
            className='menu-item menu-item-type-post_type menu-item-object-page menu-item-has-children current-menu-item current_page_item menu-item-3241'
          >
            <NavLink to='/store' className='tm-store__right-nav--link'>
              Store
            </NavLink>
            <ul className='sub-menu '>
              <li id='menu-item-4067' className='tm-store__right-nav--item'>
                <a
                  className='tm-store__right-nav--link'
                  href='https://tummachines.com/danh-muc/tops/'
                >
                  Tops
                </a>
              </li>
              <li id='menu-item-4065' className='tm-store__right-nav--item'>
                <a
                  className='tm-store__right-nav--link'
                  href='https://tummachines.com/danh-muc/denim/'
                >
                  Denim
                </a>
              </li>
              <li id='menu-item-4064' className='tm-store__right-nav--item'>
                <a
                  className='tm-store__right-nav--link'
                  href='https://tummachines.com/danh-muc/bottoms/'
                >
                  Bottoms
                </a>
              </li>
              <li id='menu-item-4066' className='tm-store__right-nav--item'>
                <a
                  className='tm-store__right-nav--link'
                  href='https://tummachines.com/danh-muc/outerwear/'
                >
                  Outerwear
                </a>
              </li>
              <li id='menu-item-4063' className='tm-store__right-nav--item'>
                <a
                  className='tm-store__right-nav--link'
                  href='https://tummachines.com/danh-muc/accessories/'
                >
                  Accessories
                </a>
              </li>
            </ul>
          </li>
          <li
            id='menu-item-3242'
            className='menu-item menu-item-type-post_type menu-item-object-page menu-item-3242'
          >
            <a
              className='tm-store__right-nav--link'
              href='https://tummachines.com/contact-us/'
            >
              Contact
            </a>
          </li>
          <li>
            <button
              className='tm-store__right-nav--cart'
              title='View your Cart'
              onClick={toggleCart}
            >
              {productsQuantity}
            </button>
          </li>
        </ul>
      </nav>
    </div>
  );
};

RightNav.propTypes = {
  toggleCart: PropTypes.func.isRequired,
  productsQuantity: PropTypes.number.isRequired
};

export default RightNav;
