import React from "react";
import { NavLink } from "react-router-dom";

import "./Nav.scss";

const Nav = () => {
  return (
    <nav className="tm-nav">
      <NavLink
        className="tm-nav__link"
        to={{
          pathname: "/"
        }}
        exact
      >
        Home
      </NavLink>
      <NavLink
        className="tm-nav__link"
        to={{
          pathname: "/store"
        }}
      >
        Store
      </NavLink>
      <NavLink className="tm-nav__link" to="/contact">
        Contact
      </NavLink>
      <NavLink className="tm-nav__link" to="/social">
        Social
      </NavLink>
      <NavLink className="tm-nav__link" to="/terms">
        Terms
      </NavLink>
    </nav>
  );
};

export default Nav;
