import React from 'react';
import { Route, Switch } from 'react-router-dom';

import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import HomePage from './home/HomePage';
import AboutPage from './about/AboutPage';
import PageNotFound from './PageNotFound';
import CoursesPage from './courses/CoursesPage';
import ManageCoursePage from './courses/ManageCoursePage';
import StorePage from './store/StorePage';
import RouteAnimation from './common/RouteAnimation';

import './App.scss';

const nestedRoute = ['/store'];

function App() {
  return (
    <div className='app'>
      <RouteAnimation nestedRoute={nestedRoute} classNames='page-animation' appear>
        <Switch>
          <Route exact path='/' component={HomePage} />
          <Route path='/store' component={StorePage} />
          <Route path='/about' component={AboutPage} />
          <Route path='/courses' component={CoursesPage} />
          <Route path='/course/:slug' component={ManageCoursePage} />
          <Route path='/course' component={ManageCoursePage} />
          <Route component={PageNotFound} />
        </Switch>
      </RouteAnimation>
      <ToastContainer autoClose={3000} hideProgressBar position='bottom-left' newestOnTop closeOnClick draggable={false} pauseOnHover />
    </div>
  );
}

export default App;
