import React, { useState, useEffect } from 'react';

import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import './../../../style/scss/page-animation.scss';
import './ProductDetails.scss';

import shareService from '../../../services/share.service';
import QuantityEditor from '../service/quantityEditor.service';


const defaultValues = {
  orderInfo: product => {
    return {
      ...product,
      sizes: product.sizes.map(item => {
        const tmp = JSON.parse(JSON.stringify(item));
        tmp.quantity = 0;
        return tmp;
      })
    };
  },
  productsQuantityTotal: 0
};

let quantityEditor = new QuantityEditor({});

function ProductDetails({ products, addToCart, match }) {
  const product = products.find(product => product.id.toString() === match.params.id.toString());

  if (!product) {
    return <></>;
  }

  const [productsQuantityTotal, setProductsQuantityTotal] = useState(defaultValues.productsQuantityTotal);

  const [orderInfo, setOrderInfo] = useState(defaultValues.orderInfo(product));

  useEffect(() => {
    quantityEditor.orderInfo = orderInfo;
    quantityEditor.setOrderInfo = setOrderInfo;
  }, [product]);

  useEffect(() => {
    setProductsQuantityTotal(quantityEditor.updateProductsQuantityTotal());
  }, [orderInfo]);

  const handleAddToCart = () => {
    addToCart(orderInfo);

    quantityEditor.resetProductQuantity();
  };

  return (
    <>
      <ul className='tm-product-images'>
        {product.images.map(image => (
          <li key={image.color}>
            <a>
              <img src={require('../../../assets/images/' + image.url)} />
            </a>
          </li>
        ))}
      </ul>

      <div className='tm-product-details'>
        <h1>{product.name} </h1>
        <div className='tm-product-details__desc'>
          <p style={{ textAlign: 'left' }}>
            Thnh phn vi :&nbsp; 98% cotton , 2% elastanes
            <br />
            _ Form o c lm theo form box tee
            <br />
            _ Vi o c x l thm 1 bc git lnh  khng gy bt m hi khi mc
            <br />_ o c dng cng ngh in In la v p nhit gip cho mc in thm vo o nhiu hn gim c s nt hnh in khi git.
          </p>
          <p style={{ textAlign: 'left' }}>
            <strong style={{ fontStyle: 'italic' }}>Ngi mu cao 1 72 nng 65kg mc size M</strong>
          </p>
          <a
            className='size-chart'
            href='https://www.tummachines.com/wp-content/uploads/2019/03/size-ao-thun.jpg'
            style={{
              color: '#D9534F',
              fontWeight: 'bold',
              textAlign: 'left',
              float: 'left'
            }}
          >
            Size Chart
          </a>{' '}
        </div>
        <table className='table vartable'>
          <thead>
            <tr>
              <th data-sort='float' className='vartable_variations pa-size'>
                <span>Size</span>
              </th>
              <th data-sort='float' className='vartable_stock'>
                <span>Stock</span>
              </th>
              <th data-sort='float' className='vartable_price'>
                <span>Price</span>
              </th>
              <th data-sort='int' className='vartable_qty'>
                <span>Quantity</span>
              </th>
            </tr>
          </thead>
          <tbody>
            {product.sizes.map((sizes, i) => {
              const isStocking = sizes.quantity > 0;

              return (
                <tr key={i} className='instock is_purchasable'>
                  <td className='optionscol attribute_pa_size'>{sizes.name}</td>
                  <td className={'stockcol ' + (isStocking ? 'green' : 'red')}>
                    <span className='instock'>{isStocking ? 'Cn Hng' : 'Ht Hng'}</span>
                  </td>
                  <td className='pricecol'>
                    <span className='woocommerce-Price-amount amount'>
                      {shareService.formatMoney(sizes.price, 0)}&nbsp;
                      <span className='woocommerce-Price-currencySymbol'>VN</span>
                    </span>
                  </td>
                  <td className={'qtycol ' + (isStocking ? '' : 'disable')}>
                    <div className='tm-product-details__quantity'>
                      <div
                        className='tm-product-details__operators'
                        onMouseDown={quantityEditor.changeProductQuantityMouseDown.bind(quantityEditor, quantityEditor.decreaseProductQuantity.bind(quantityEditor, i))}
                        onMouseUp={quantityEditor.changeProductQuantityMouseUp}
                      >
                        -
                      </div>
                      <input
                        type='number'
                        name='var_quantity'
                        title='Qty'
                        className='input-text qty text'
                        value={orderInfo.sizes[i].quantity}
                        onChange={quantityEditor.onChangeProductQuantity.bind(quantityEditor, i)}
                        max='999'
                      />
                      <div
                        className='tm-product-details__operators'
                        onMouseDown={quantityEditor.changeProductQuantityMouseDown.bind(quantityEditor, quantityEditor.increaseProductQuantity.bind(quantityEditor, i))}
                        onMouseUp={quantityEditor.changeProductQuantityMouseUp}
                      >
                        +
                      </div>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>

        <button className='tm-product-details__add-cart-btn' onClick={handleAddToCart}>
          Thm vo gi hng
          <span className='vt_products_count'> ({productsQuantityTotal})</span>
        </button>
      </div>
    </>
  );
}

ProductDetails.propTypes = {
  products: PropTypes.array.isRequired,
  match: PropTypes.object.isRequired,
  addToCart: PropTypes.func.isRequired
};

function mapStateToProps() {
  return {};
}

const mapDispatchToProps = {};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProductDetails);
