import React, { useEffect, useState } from "react";
import { Route, Switch } from "react-router-dom";
import PropTypes from "prop-types";

import { toast } from "react-toastify";

import { connect } from "react-redux";
import { loadProducts } from "../../redux/actions/storeActions";

import Spinner from "../common/spinner/Spinner";
import PageNotFound from "./../PageNotFound";

import ProductList from "./product/ProductList";
import LeftNav from "./nav/LeftNav";
import RightNav from "./nav/RightNav";
import ProductDetails from "./product/ProductDetails";
import CartDetails from "./cart/CartDetails";
import SideCart from "./cart/SideCart";
import CheckOut from "./checkout/CheckOut";

import "./../../style/scss/page-animation.scss";
import "./StorePage.scss";

import intervalService from "../../services/interval.service";
import shareService from "../../services/share.service";

function StorePage({ products, loadProducts, match, location, loading }) {
  const [isShowCart, setIsShowCart] = useState(false);
  const [isInit, setIsInit] = useState(false);
  const [cart, setCart] = useState({
    products: [],
    productsQuantity: 0,
    total: 0
  });

  useEffect(() => {
    if (products.length === 0) {
      loadProducts().catch(error => {
        alert("Loading products failed\n" + error);
      });
    }
  }, [products.length]);

  useEffect(() => {
    if (!isInit && !cart.products.length) {
      var sessionCart = JSON.parse(sessionStorage.getItem("cart"));

      if (!cart.products.length && sessionCart && sessionCart.products.length) {
        setCart(sessionCart);
      }

      setIsInit(true);
    } else {
      sessionStorage.setItem("cart", JSON.stringify(cart));
    }
  }, [cart]);

  const toggleCart = () => {
    setIsShowCart(!isShowCart);
  };

  const addToCart = productInfo => {
    let newCart;

    const productInfoClone = shareService.deepClone(productInfo),
      isAlreadyHadProductInCart =
        cart.products.findIndex(item => productInfoClone.id === item.id) > -1,
      isQuantityMoreThan0 = productInfoClone.sizes.find(
        size => size.quantity > 0
      );

    if (!isQuantityMoreThan0) {
      toast("Vui lng nhp s lng sn phm cn thm.");

      return;
    }

    if (isAlreadyHadProductInCart) {
      newCart = {
        ...cart,
        products: cart.products.map(currentProductInfo => {
          if (productInfoClone.id === currentProductInfo.id) {
            currentProductInfo.sizes = currentProductInfo.sizes.map(
              (size, i) => {
                const newSize = {
                  ...size,
                  quantity: size.quantity + productInfoClone.sizes[i].quantity
                };

                return newSize;
              }
            );
          }

          return currentProductInfo;
        })
      };
    } else {
      newCart = {
        ...cart,
        products: [...cart.products, { ...productInfoClone }]
      };
    }

    let newProductsQuantityInCart = 0;
    let newTotal = 0;
    newCart.products.map(product => {
      product.sizes.map(size => {
        newProductsQuantityInCart += size.quantity;
        newTotal += size.quantity * size.price;
      });
    });

    newCart = {
      ...newCart,
      productsQuantity: newProductsQuantityInCart,
      total: newTotal
    };

    setCart(newCart);

    toast(" thm vo gi hng.");
  };

  const removeProductFromCart = (productId, sizeName) => {
    let targetProductCost,
      targetProductQuantity,
      isAllSize0Quantity = true;

    const newTargetProductSizes = cart.products
        .find(item => item.id === productId)
        .sizes.map(item => {
          item.name === sizeName &&
            (targetProductCost = item.quantity * item.price) &&
            (targetProductQuantity = item.quantity) &&
            (item.quantity = 0);

          item.quantity > 0 && (isAllSize0Quantity = false);
          return item;
        }),
      newCart = {
        ...cart,
        productsQuantity: cart.productsQuantity - targetProductQuantity,
        total: cart.total - targetProductCost
      };

    newCart.products.find(
      item => item.id === productId
    ).sizes = newTargetProductSizes;

    if (isAllSize0Quantity) {
      newCart.products = newCart.products.filter(item => item.id !== productId);
    }

    setCart(newCart);

    toast(" xa khi gi hng.");

    return isAllSize0Quantity;
  };

  return (
    <>
      <div
        className="tm-store page-animation"
        onMouseUp={intervalService.customClearInterval}
      >
        <LeftNav />
        {loading && location.pathname !== "/store/cart" ? (
          <Spinner />
        ) : (
          <>
            <Switch>
              <Route
                exact
                path={match.path}
                render={props => <ProductList {...props} products={products} />}
              />
              <Route
                path={match.path + "/product/:id"}
                render={props => (
                  <ProductDetails
                    {...props}
                    products={products}
                    addToCart={addToCart}
                  />
                )}
              />
              <Route
                path={match.path + "/cart"}
                render={props => (
                  <CartDetails
                    {...props}
                    cart={cart}
                    setCart={setCart}
                    removeProductFromCart={removeProductFromCart}
                  />
                )}
              />
              <Route
                path={match.path + "/checkout"}
                render={props => (
                  <CheckOut
                    {...props}
                    cart={cart}
                    setCart={setCart}
                    removeProductFromCart={removeProductFromCart}
                  />
                )}
              />
              <Route component={PageNotFound} />
            </Switch>
            <SideCart
              isShowCart={isShowCart}
              toggleCart={toggleCart}
              cart={cart}
              removeProductFromCart={removeProductFromCart}
            />
          </>
        )}
        <RightNav
          toggleCart={toggleCart}
          productsQuantity={cart.productsQuantity}
        />
      </div>
    </>
  );
}

StorePage.propTypes = {
  products: PropTypes.array.isRequired,
  loadProducts: PropTypes.func.isRequired,
  loading: PropTypes.bool.isRequired,
  match: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired
};

function mapStateToProps(state) {
  return {
    products: state.products,
    loading: state.apiCallsInProgress > 0
  };
}

const mapDispatchToProps = {
  loadProducts
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(StorePage);
