let interval = null;
let timeout = null;

const customSetInterval = (func, timeOut) => {
  timeout = setTimeout(() => {
    interval = setInterval(func, timeOut);
  }, 300);
};

const customClearInterval = () => {
  clearTimeout(timeout);
  clearInterval(interval);
};

export default {
  interval,
  customSetInterval,
  customClearInterval
};
