import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";

import shareService from "../../../services/share.service";
import QuantityEditor from "../service/quantityEditor.service.js";

function CartDetails({ cart, setCart, removeProductFromCart }) {
  const [quantityEditor, setQuantityEditor] = useState([]);
  const [isInit, setIsInit] = useState(false);

  const setOrderInfo = (cart, quantityEditor) => {
    let newProductsQuantityInCart = 0;
    let newTotal = 0;
    quantityEditor.forEach(item => {
      item.orderInfo.sizes.forEach(size => {
        newProductsQuantityInCart += size.quantity;
        newTotal += size.quantity * size.price;
      });
    });

    const newCart = {
      ...cart,
      products: quantityEditor.map(item => item.orderInfo),
      productsQuantity: newProductsQuantityInCart,
      total: newTotal
    };
    setCart(newCart);
    quantityEditor.length && setCart(newCart);
  };

  useEffect(() => {
    //init quantityEdior
    if (!quantityEditor.length && cart.products.length) {
      initQuantityEditor();
    } else {
      if (!cart.products.length) {
        setQuantityEditor([]);
        setIsInit(false);
      }
    }
  }, [cart]);

  useEffect(() => {
    if (quantityEditor.length && !isInit) {
      updateSetInfo();

      setIsInit(true);
    }
  });

  const initQuantityEditor = () => {
    let initQuantityEditor = [];

    cart.products.forEach((productInfo, i) => {
      const newQuantityEditor = new QuantityEditor({ isAllow0: false });

      newQuantityEditor.orderInfo = shareService.deepClone(productInfo);
      initQuantityEditor.push(newQuantityEditor);
    });

    setQuantityEditor(initQuantityEditor);
  };

  const removeProductFromQuantityEditors = (productI, productId, sizeName) => {
    removeProductFromCart(productId, sizeName);

    let isAllSize0Quantity = true;

    quantityEditor[productI].orderInfo.sizes = quantityEditor
        .find(item => item.orderInfo.id === productId)
        .orderInfo.sizes.map(item => {
          item.name === sizeName &&
            (item.quantity = 0);

          item.quantity > 0 && (isAllSize0Quantity = false);
          return item;
        });

    if (isAllSize0Quantity) {
      setQuantityEditor(quantityEditor.filter(item => item.orderInfo.id !== productId));
    }
    console.log(quantityEditor);
  };

  const updateSetInfo = () => {
    let initQuantityEditor = [...quantityEditor];

    initQuantityEditor.forEach((item, i) => {
      initQuantityEditor[i].setOrderInfo = setOrderInfo.bind(
        this,
        cart,
        quantityEditor
      );
    });

    setQuantityEditor(initQuantityEditor);
  };

  return (
    <form className="tm-cart-details">
      <NavLink to="/store" className="tm-cart-details__back-btn">
         Tip tc mua sm
      </NavLink>
      <table className="tm-cart-details__products-info" cellSpacing={0}>
        <tbody>
          {cart.products.map((product, productI) =>
            !quantityEditor.length ? (
              <tr key={productI + " none"} />
            ) : (
              product.sizes.map((size, i) =>
                size.quantity <= 0 ? (
                  <tr key={productI + i + " none"} />
                ) : (
                  <tr key={product.id + size.name}>
                    <td>
                      <img
                        width={90}
                        height={90}
                        src={require("../../../assets/images/" +
                          product.images[0].url)}
                        className="attachment-shop_thumbnail size-shop_thumbnail wp-post-image"
                      />
                    </td>
                    <td>
                      <h6 className="scale-when-hover">
                        <NavLink to={"/store/product/" + product.id}>
                          {product.name + " - Size " + size.name}
                        </NavLink>
                      </h6>
                    </td>
                    <td>
                      <span>
                        {shareService.formatMoney(size.price, 0)}&nbsp;
                        <span>VN</span>
                      </span>
                    </td>
                    <td className="tm-cart-details__products-quantity">
                      <div>
                        <input
                          type="button"
                          defaultValue="-"
                          onMouseDown={quantityEditor[
                            productI
                          ].changeProductQuantityMouseDown.bind(
                            quantityEditor[productI],
                            quantityEditor[
                              productI
                            ].decreaseProductQuantity.bind(
                              quantityEditor[productI],
                              i
                            )
                          )}
                          onMouseUp={
                            quantityEditor[productI]
                              .changeProductQuantityMouseUp
                          }
                        />
                        <input
                          type="number"
                          value={size.quantity}
                          onChange={quantityEditor[
                            productI
                          ].onChangeProductQuantity.bind(
                            quantityEditor[productI],
                            i
                          )}
                        />
                        <input
                          type="button"
                          defaultValue="+"
                          onMouseDown={quantityEditor[
                            productI
                          ].changeProductQuantityMouseDown.bind(
                            quantityEditor[productI],
                            quantityEditor[
                              productI
                            ].increaseProductQuantity.bind(
                              quantityEditor[productI],
                              i
                            )
                          )}
                          onMouseUp={
                            quantityEditor[productI]
                              .changeProductQuantityMouseUp
                          }
                        />
                      </div>
                    </td>
                    <td>
                      <span>
                        {shareService.formatMoney(
                          size.price * size.quantity,
                          0
                        )}
                        &nbsp; VN
                      </span>
                    </td>
                    <td>
                      <a
                        href="javascript:void(0);"
                        className="tm-remove-btn"
                        title="Remove this item"
                        onClick={() =>
                          removeProductFromQuantityEditors(
                            productI,
                            product.id,
                            size.name
                          )
                        }
                      >
                        
                      </a>
                    </td>
                  </tr>
                )
              )
            )
          )}
        </tbody>
      </table>

      <div className="tm-cart-details__coupon-container">
        <input
          type="text"
          name="coupon_code"
          className="tm-cart-details__coupon-code"
          placeholder="Coupon code"
        />
        <input
          type="submit"
          className="tm-button"
          name="apply_coupon"
          value="Apply Coupon"
        />
      </div>

      <aside className="tm-cart-details__bill-container">
        <table className="tm-cart-details__bill-info" cellSpacing={0}>
          <tbody>
            <tr className="cart-subtotal">
              <th colSpan={3}>Tm Tnh:</th>
              <td>
                <span className="woocommerce-Price-amount amount">
                  {shareService.formatMoney(cart.total, 0)}&nbsp;
                  <span className="woocommerce-Price-currencySymbol">VN</span>
                </span>
              </td>
            </tr>
            <tr className="shipping">
              <th colSpan={3}>Shipping:</th>
              <td data-title="Shipping">
                COD{" "}
                <input
                  type="hidden"
                  name="shipping_method[0]"
                  data-index={0}
                  id="shipping_method_0"
                  defaultValue="flat_rate:11"
                  className="shipping_method"
                />
              </td>
            </tr>
            <tr className="order-total">
              <th colSpan={3}>Tng cng:</th>
              <td>
                <strong>
                  <span className="woocommerce-Price-amount amount">
                    {shareService.formatMoney(cart.total, 0)}&nbsp;
                    <span className="woocommerce-Price-currencySymbol">
                      VN
                    </span>
                  </span>
                </strong>
              </td>
            </tr>
          </tbody>
        </table>

        <div className="tm-cart-details__func-btns">
          <NavLink
            to="/store/checkout"
            className="tm-button tm-cart-details__checkout-btn"
          >
            Thanh ton
          </NavLink>
        </div>
      </aside>
    </form>
  );
}

CartDetails.propTypes = {
  cart: PropTypes.object.isRequired,
  setCart: PropTypes.func.isRequired,
  removeProductFromCart: PropTypes.func.isRequired
};

export default CartDetails;
