import intervalService from "../../../services/interval.service";

const config = {
  changeProductQuantityTimeOut: 100
};

export default class QuantityEditor {
  constructor({ isAllow0 = true, maxProductQuantityLength = 3 }) {
    this._isAllow0 = isAllow0;
    this.maxProductQuantityLength = maxProductQuantityLength;
  }

  set orderInfo(orderInfo) {
    this._orderInfo = orderInfo;
  }

  get orderInfo() {
    return this._orderInfo;
  }

  set setOrderInfo(setOrderInfo) {
    this._setOrderInfo = setOrderInfo;
  }

  updateProductsQuantityTotal() {
    const newProductsQuantityTotal = this._orderInfo.sizes.reduce(
      (current, next) => current + next.quantity,
      0
    );

    return newProductsQuantityTotal;
  }

  changeProductQuantity(productIndex, cb) {
    const newOrderInfo = {
      ...this._orderInfo,
      sizes: this._orderInfo.sizes.map((item, i) => {
        productIndex === i && cb(item);
        return item;
      })
    };

    this._setOrderInfo(newOrderInfo);
  }

  decreaseProductQuantity(productIndex) {
    this.changeProductQuantity(productIndex, item => {
      const newProductQuantity = item.quantity - 1;

      newProductQuantity.toString().length <= 3 &&
        item.quantity > 0 &&
        (item.quantity =
          (!this._isAllow0 && newProductQuantity === 0 && 1) ||
          newProductQuantity);
    });
  }

  increaseProductQuantity(productIndex) {
    this.changeProductQuantity(productIndex, item => {
      const newProductQuantity = item.quantity + 1;

      newProductQuantity.toString().length <= 3 &&
        (item.quantity = newProductQuantity);
    });
  }

  changeProductQuantityMouseDown(cb) {
    cb();
    intervalService.customSetInterval(cb, config.changeProductQuantityTimeOut);
  }

  changeProductQuantityMouseUp() {
    intervalService.customClearInterval();
  }

  onChangeProductQuantity(productIndex, e) {
    const value = e.target.value || (this._isAllow0 ? "0" : "1");

    this.changeProductQuantity(productIndex, item => {
      value.length <= 3 &&
        !isNaN(Number(value)) &&
        (item.quantity = parseInt(value, 10));
    });
  }

  resetProductQuantity() {
    const newOrderInfo = {
      ...this._orderInfo,
      sizes: this._orderInfo.sizes.map(item => {
        item.quantity = 0;
        return item;
      })
    };

    this._setOrderInfo(newOrderInfo);
  }
}
