import React from "react";
import PropTypes from "prop-types";

import { connect } from "react-redux";
import { NavLink } from "react-router-dom";

import shareService from "../../../services/share.service";

import "./cart.scss";

function SideCart({ isShowCart, toggleCart, cart, removeProductFromCart }) {
  return (
    <>
      <nav className={"tm-cart " + (isShowCart ? "tm-cart--show" : "")}>
        <header className='tm-cart__header'>
          <a
            href='#'
            className={
              "tm-cart__close-btn " + (isShowCart ? "opacity1" : "opacity0")
            }
            title='Close'
            onClick={toggleCart}
          >
            <svg
              xmlns='http://www.w3.org/2000/svg'
              version='1.1'
              x={0}
              y={0}
              width={12}
              height={12}
              viewBox='1.1 1.1 12 12'
              enableBackground='new 1.1 1.1 12 12'
              xmlSpace='preserve'
            >
              <path
                style={{ fill: "#fff" }}
                d='M8.3 7.1l4.6-4.6c0.3-0.3 0.3-0.8 0-1.2 -0.3-0.3-0.8-0.3-1.2 0L7.1 5.9 2.5 1.3c-0.3-0.3-0.8-0.3-1.2 0 -0.3 0.3-0.3 0.8 0 1.2L5.9 7.1l-4.6 4.6c-0.3 0.3-0.3 0.8 0 1.2s0.8 0.3 1.2 0L7.1 8.3l4.6 4.6c0.3 0.3 0.8 0.3 1.2 0 0.3-0.3 0.3-0.8 0-1.2L8.3 7.1z'
              />
            </svg>
          </a>
          <h6>Gi Hng</h6>
        </header>
        <div className='custom_scroll' id='side-cart-scroll'>
          <div>
            <ul className='tm-cart__product-list'>
              {cart.products.map(product => {
                return product.sizes.map(size => {
                  const isQuantityMoreThan0 = size.quantity > 0;

                  return !isQuantityMoreThan0 ? (
                    ""
                  ) : (
                    <li
                      key={product.id + size.name}
                      className='tm-cart__product'
                    >
                      <figure className='product__image'>
                        <a href='https://www.tummachines.com/shop/2030-ad-athena-tshirt-black/?attribute_pa_size=l'>
                          <img
                            width={90}
                            height={90}
                            src={require("../../../assets/images/" +
                              product.images[0].url)}
                            className='attachment-shop_thumbnail size-shop_thumbnail wp-post-image'
                            srcSet='//www.tummachines.com/wp-content/uploads/2019/04/athena-den-6-90x90.jpg 90w, //www.tummachines.com/wp-content/uploads/2019/04/athena-den-6-150x150.jpg 150w'
                            sizes='(max-width: 90px) 100vw, 90px'
                          />
                        </a>{" "}
                      </figure>
                      <div className='product__content'>
                        <h5>
                          <a href='https://www.tummachines.com/shop/2030-ad-athena-tshirt-black/'>
                            {product.name + " - Size "}
                            <strong>{size.name}</strong>
                          </a>
                        </h5>
                        <span className='product__quantity'>
                          {size.quantity}
                        </span>
                        <span className='product__cross'></span>
                        <div className='product__price'>
                          <span className='woocommerce-Price-amount amount'>
                            {shareService.formatMoney(size.price, 0)}
                            &nbsp;
                            <span className='woocommerce-Price-currencySymbol'>
                              VN
                            </span>
                          </span>
                        </div>{" "}
                      </div>
                      <a
                        href='javascript:void(0);'
                        className='tm-remove-btn'
                        title='Remove this item'
                        onClick={() =>
                          removeProductFromCart(product.id, size.name)
                        }
                      >
                        
                      </a>
                    </li>
                  );
                });
              })}
            </ul>
          </div>
        </div>
        <div className='tm-cart__subtotal'>
          Tm Tnh
          <span>
            <span className='woocommerce-Price-amount amount'>
              {shareService.formatMoney(cart.total, 0)}&nbsp;
              <span className='woocommerce-Price-currencySymbol'>VN</span>
            </span>
          </span>
        </div>
        <div className='tm-cart__actions'>
          <NavLink
            to={{
              pathname: "/store/cart"
            }}
            title='Cart'
            className='tm-button'
            onClick={toggleCart}
          >
            Xem Gi Hng
          </NavLink>
          <NavLink
            to={{
              pathname: "/store/checkout"
            }}
            title='Check Out'
            className='tm-button'
            onClick={toggleCart}
          >
            Thanh Ton
          </NavLink>
        </div>
      </nav>
      {isShowCart && <div className='tm-cart__overlay' onClick={toggleCart} />}
    </>
  );
}

SideCart.propTypes = {
  loading: PropTypes.bool.isRequired,
  isShowCart: PropTypes.bool.isRequired,
  toggleCart: PropTypes.func.isRequired,
  cart: PropTypes.object.isRequired,
  removeProductFromCart: PropTypes.func.isRequired
};

function mapStateToProps(state) {
  return {
    courses:
      state.authors.length === 0
        ? []
        : state.courses.map(course => {
            return {
              ...course,
              authorName: state.authors.find(a => a.id === course.authorId).name
            };
          }),
    authors: state.authors,
    loading: state.apiCallsInProgress > 0
  };
}

const mapDispatchToProps = {};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SideCart);
