import React from "react";
import { NavLink } from "react-router-dom";

const LeftNav = () => {
  return (
    <div className='tm-store__left-nav'>
      <NavLink to='/'>
        <img
          src='https://tummachines.com/wp-content/uploads/2018/08/tummachines-logo-200.png'
          className='logoimg bg--light'
          alt='Tum Machines Clo'
        />
      </NavLink>
    </div>
  );
};

export default LeftNav;
