import React from 'react';
import { Route, Switch } from 'react-router-dom';
import PropTypes from 'prop-types';

import { CSSTransition, TransitionGroup } from 'react-transition-group';

function RouteAnimation({ nestedRoute, appear, classNames, children }) {
  let currentURL = '';

  return (
    <Switch>
      <Route
        render={({ location }) => {
          const { pathname } = location;

          const isApplyAnimation = (nestedRoute = [], pathname = '', currentURL = '') => {
            let isApplyAnimation = true;
            nestedRoute.map(item => {
              isApplyAnimation = !(pathname.includes(item) && currentURL.includes(item));
            });

            return isApplyAnimation;
          };

          if (isApplyAnimation(nestedRoute, pathname, currentURL)) {
            currentURL = pathname;
          }

          return (
            <TransitionGroup component={null}>
              <CSSTransition
                key={currentURL}
                classNames={classNames}
                appear={appear}
                timeout={{
                  enter: 1000,
                  exit: 1000
                }}
              >
                <Route
                  location={location}
                  render={() => (
                    children
                  )}
                />
              </CSSTransition>
            </TransitionGroup>
          );
        }}
      />
    </Switch>
  );
}

RouteAnimation.propTypes={
  nestedRoute: PropTypes.array.isRequired,
  appear: PropTypes.bool.isRequired,
  classNames: PropTypes.string.isRequired,
  children: PropTypes.object.isRequired
}

export default RouteAnimation;
