import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

import './ProductCard.scss';

const ProductCard = ({ product }) => {
  console.log(product.images[0]);

  return (
    <div className="tm-product-card">
      <figure className="tm-product-card__image-container">
        <NavLink
          to={{
            pathname: '/store/product/' + product.id,
            product: product
          }}
          title={product.name}
        >
          <span
            className="tm-product-card__hover-image"
            style={{
              backgroundImage: 'url(' + require('../../../assets/images/' + product.hoverImage) + ')'
            }}
          />
          <img className="tm-product-card__image" src={require('../../../assets/images/' + product.images[0].url)} alt={product.name} />
        </NavLink>
      </figure>
      <div className="tm-product-card__info">
        <h3>
          <a href="https://tummachines.com/shop/el-capital-tshirt-yellow/" title="EL CAPITAL TSHIRT  YELLOW">
            {product.name}
          </a>
        </h3>
        <div className="tm-product-card__price">
          320,000&nbsp;
          <span className="woocommerce-Price-currencySymbol">VN</span>
        </div>
      </div>
    </div>
  );
};

ProductCard.propTypes = {
  product: PropTypes.object.isRequired
};

export default ProductCard;
